import _ from 'lodash';

export const formatColor = color => {
  switch (color) {
    case 'red':
      return 'rgb(250, 138, 106)';
    case 'green':
      return 'rgb(166, 233, 111)';
    case 'yellow':
      return 'rgb(250, 215, 118)';
    case 'blue':
      return 'rgb(106, 213, 242)';
    default:
      return 'gray';
  }
}

export const formatCornerName = cornerName => {
  return cornerName.split('').reverse().join('')
    .replace('l', '左')
    .replace('r', '右')
    .replace('t', '上')
    .replace('b', '下')
    + '角';
}

export const cellForWalk = ({tl, tr, bl, br}, position=-1) => ([position, tl, tr, bl, br])
export const cellsForWalk = cells => cells.map(cellForWalk)

// const cellNameIndex = ['t', 'tl', 'tr', 'l', 'c', 'r', 'bl', 'br', 'b'];
const gradeLeaves = {
  60: [[1, 2], [3, 4], [4, 5], [null, 6], [6, 7], [7, null], [null, 8], [8, null]],
  50: [[1, 2], [null, 4], [4, null], [null, null], [6, 7], [null, null], [null, 8], [8, null]]
};

export const getPlanCells = (baseCells, positions, grade) => {
  const planCells = positions.map(item => {
    return {
      ..._.pick(baseCells[item], ['core', 'tl', 'tr', 'bl', 'br']),
      baseCellsIndex: item
    };
  });
  const liteCells = cellsForWalk(planCells);
  return planCells.map((cell, index) => {
    let lls = false;
    let lrs = false;
    cmpLeaf(cellForWalk(cell), index, liteCells, grade, () => {
      lls = true;
    }, () => {
      lrs = true;
    })
    return {...cell, lls, lrs};
  });
};

const cmpLeaf = (cell, index, cells, grade, onMatchLeft, onMatchRight) => {
  const leaves = gradeLeaves[grade];
  if (!leaves[index]) {
    return;
  }
  const [left, right] = leaves[index];
  if (left && cell[3] && cell[3] === cells[left][2]) {
    onMatchLeft();
  }
  if (right && cell[4] && cell[4] === cells[right][1]) {
    onMatchRight();
  }
}

export const walkCells = (cells, grade) => {
  const sum = {
    positions: [],
    red: 0,
    green: 0,
    yellow: 0,
    blue: 0,
    total: 0
  };
  cells.forEach((cell, index) => {
    sum.positions.push(cell[0]);
    cmpLeaf(cell, index, cells, grade, () => {
      sum[cell[3]] += 1;
      sum.total += 1;
    }, () => {
      sum[cell[4]] += 1;
      sum.total += 1;
    })
  });
  return sum;
}

export const getTopList = (plans, sort) => {
  return _.sortedUniqBy(
    _.sortBy(plans, [...sort, 'total']),
    item => ['total', 'red', 'green', 'yellow', 'blue'].reduce((ret, type) => ret + '-' + item[type], '')
  )
    .slice(-100)
    .reverse();
}

